.title{
  text-align: center;
}

.images{
  display: grid;
  grid-template-columns: repeat(4,1fr);
}

.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.images__box{
  width: 200px;
  height: 300px;
  display: grid;
  place-items: center;
  box-shadow: 0.1px 0.1px 5px #000;
  border-radius: 8px;
  padding: 10px;

}

.images__box img{
  width: 100%;
  transition: all 0.5s;
  filter: drop-shadow(0.1px 0.1px 5px #000);
}

.images__box:hover img{
  transition: all 0.5s;
  transform: rotate(10deg) scale(120%);
}

.images__box p{
  font-weight: 800;
}